import Token from "../Token";
import axios from 'axios';
import Url from "Services/ServerUrl";

export interface Format{
    network_error : boolean,
    status        : number,
    data          : any,
}

interface handleError{
    response : any,
    format   : any,
    callback : any,
    auth     ?: boolean,
    params   ?: any,
}

interface recall{
    error : any,
    callback : any,
    format   : any,
    params   ?: any,
}

export class StaticMethods{


    static async recall(a:recall){
        let format = a.format;

        if(!a.error.response){
            format.network_error = true;
            return format;
        }

        if(a.error.response.status === 401){
            await new Promise( resolve => setTimeout(resolve, 60000) );
            if(a.params){
                return a.callback(a.params);
            }

            return a.callback();
        }

        format.status = a.error.response.status;
        format.data   = a.error.response.data;
        
        return format;        

    }


    static async handle_error(b:handleError){
        const format = b.format;
        //if network error
        
        if(!b.response){
           format.network_error = true;
           return format;
        }
        
        //refresh token if expired, skip if token is invalid
        // if( b.auth !== undefined && b.auth === true && b.response.status === 401 ){

        //     if(Token.valid()){
        //         if(b.params === undefined || b.params === null){
        //             return this.retry(b.callback,undefined);
        //         }

        //         return this.retry(b.callback,b.params);
        //     }
        // }



        if(b.response.status === 401 ){

            // if(Token.exist()){
            //     await this.refresh().then((response) => {
            //         Token.save(response.data.token);
            //     }).catch(() => {
            //         Token.remove();
            //         format.status = 401;
            //         format.data   = b.response.data;
            //         return format;
            //     });
            // }

            if(!b.params){
                return this.retry(b.callback,undefined);
            }

            return this.retry(b.callback,b.params);
            
        }


        //if too many request
        if(b.response.status === 429){
            let a:any = localStorage.getItem(Token.get());
            
            if(!a){
                a = 0;
            }

            if( parseInt(a) <= 3){
                return this.retry(b.callback,b.params);
            }
        }
        
        //return this if above condition doesn't met and if token is invalid
        format.status = b.response.status;
        format.data   = b.response.data;
        return format;
    }


    static async retry(callback:Function,params:any){
        if(Token.exist()){
            if(!localStorage.getItem(Token.get())){
                localStorage.setItem(Token.get(),String(0));
            }else{
                let a:any = localStorage.getItem(Token.get());
                let counter = parseInt(a);
                localStorage.setItem(Token.get(),String(counter+1));
            }
        }
        
        await this.delay(1000);

        if(!params){
            return callback();
        }

        return callback(params);
    }


    static async delay(ms:number){
        return new Promise( resolve => setTimeout(resolve, ms) );
    }


    static header(token?:any){

        if(!token){
            return {
                'Content-Type' : 'application/json',
                'Accept'       :  'application/json',
            };
        }
        
        return {
            'Content-Type'         :    'application/json',
            'Accept'               :    'application/json',
            'Authorization'        :    'Bearer '+token
        };
    }

    static sleeper = (ms:number) => {
        return function(x:any) {
            return new Promise(resolve => setTimeout(() => resolve(x), ms));
        };
    }

    public static async refresh(){
        let format:Format = {
            network_error : false,
            status : 0,
            data : '',
        }
        const header = this.header(Token.get());

        await axios({
            method  :   "POST",
            url     :   Url.refresh,
            headers :   header,
        }).then( response => {
            format.status = response.status;
            format.data = response.data;
        }).catch( () =>{
            format= {
                network_error : false,
                status : 0,
                data : '',
            }
        });

        return format;

    }
}